import { TitleCasePipe } from '@angular/common';
import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { Router } from '@angular/router';
import { AuthService } from 'src/app/core/services/auth.service';
import { LoginService } from 'src/app/core/services/login.service';
import { OtpVerifyService } from 'src/app/core/services/otp-verify.service';
import { RoleSignupService } from 'src/app/core/services/role-signup.service';
import { NgxSpinnerService } from "ngx-spinner";


@Component({
  selector: 'app-role-selection',
  templateUrl: './role-selection.component.html',
  styleUrls: ['./role-selection.component.scss']
})
export class RoleSelectionComponent implements OnInit {
  // CustLogintoggle: boolean;

  constructor(private router: Router,
    private fb: FormBuilder,
    private authService: AuthService,
    private loginService: LoginService,
    public otpVerify: OtpVerifyService,
    private http: HttpClient,
    public roleSignupService: RoleSignupService,
    private spinner: NgxSpinnerService,
    private titleCasePipe: TitleCasePipe) { }

  ngOnInit(): void {
  }

  onCustomerClick() {
    this.router.navigate(['/auth/login'], { queryParams: { CustLogintoggle: 'true' } });
  }
  
  onRetailerClick() {
    this.router.navigate(['/auth/login'], { queryParams: { CustLogintoggle: 'false' } });
  }

}
