import { TitleCasePipe } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { LoginService } from 'src/app/core/services/login.service';
import { ToastNotificationService } from 'src/app/core/services/toast-notification.service';

@Component({
  selector: 'app-verify-otp-forgot-password',
  templateUrl: './verify-otp-forgot-password.component.html',
  styleUrls: ['./verify-otp-forgot-password.component.scss']
})
export class VerifyOtpForgotPasswordComponent implements OnInit {

  VerifyOTPForm:any;
  public submitted: boolean;
  constructor(private formBuilder: FormBuilder, private toastService:ToastNotificationService,private loginService: LoginService,private router: Router,
    private titleCasePipe: TitleCasePipe) { }

  ngOnInit(): void {
    this.VerifyOTPForm = this.formBuilder.group({
			forgetpass: ["",[Validators.required,Validators.email]],
      otp:["",[Validators.required]],
    });
  }
  get f(){
    return this.VerifyOTPForm.controls;
  }
  postforgotpassword(){
    this.submitted=true;
    if(this.VerifyOTPForm.invalid){
      return false;
    }
    else{
      let body={
        "email" : this.VerifyOTPForm.controls.forgetpass.value,
        "otp" : this.VerifyOTPForm.controls.otp.value

      }
      this.loginService.verifyforgetOtp(body).then((res)=>{
        this.toastService.openSnackBar("OTP Verified Successfully");
        this.router.navigate(["/auth/change-forgot-password"]);
      }, 
      (err) => {
    
        if(err.error.expose){
          this.toastService.openErrorSnackBar(this.titleCasePipe.transform(err.error.error_message));
        }
        else{
          this.toastService.openErrorSnackBar("Something Went Wrong.");
        }
        
      })
  
    }

  }
  numberOnly(event): boolean {
    const charCode = (event.which) ? event.which : event.keyCode;
    if (charCode > 31 && (charCode < 48 || charCode > 57)) {
      return false;
    }
    return true;

  }

}
