import { Injectable } from '@angular/core';
import { HttpService } from './http.service';
import { UrlConstants } from '../../_helpers/url-constants' 

@Injectable({
  providedIn: 'root'
})
export class RoleSignupService {

  constructor(public httpClient:HttpService) { }

  sendRoleUsersOtp(data: {}): any {
    return new Promise((resolve, reject) => {
      this.httpClient.call(data, UrlConstants.sendRoleUserOtp ,  'POST').subscribe(
        (res) => resolve(res),
        (err) => reject(err)
      );
    });
  }
  verifyRoleUsers(data: {}): any {
    return new Promise((resolve, reject) => {
      this.httpClient.call(data, UrlConstants.roleUserVerify,  'POST').subscribe(
        (res) => resolve(res),
        (err) => reject(err)
      );
    });
  }
  getUserBasedRoleByEmailId(data: {},emailId): any {
    return new Promise((resolve, reject) => {
      this.httpClient.call(data, UrlConstants.getUserBasedRole + emailId, 'GET').subscribe(
        (res) => resolve(res),
        (err) => reject(err)
      );
    });
  }
}
