import { Injectable } from '@angular/core';
import { UrlConstants } from 'src/app/_helpers/url-constants';
import { HttpService } from './http.service';

@Injectable({
  providedIn: 'root'
})
export class LoginService {

  constructor(public httpClient:HttpService) { }

  postSendOtp(data: {}): any {
    return new Promise((resolve, reject) => {
      this.httpClient.call(data, UrlConstants.sendCustomerOtp ,  'POST').subscribe(
        (res) => resolve(res),
        (err) => reject(err)
      );
    });
  }
  // postVerifyOtp(data: {}): any {
  //   return new Promise((resolve, reject) => {
  //     this.httpClient.call(data, UrlConstants.verifyCustomerOtp ,  'POST').subscribe(
  //       (res) => resolve(res),
  //       (err) => reject(err)
  //     );
  //   });
  // }
  forgotpassword(data:{}){
    return new Promise((resolve, reject) => {
      this.httpClient.call(data, UrlConstants.forgotPassword ,'POST').subscribe(
        (res) => resolve(res),
        (err) => reject(err)
      );
    });

  }
  verifyforgetOtp(data:{}){
    return new Promise((resolve, reject) => {
      this.httpClient.call(data, UrlConstants.verifyforgetOTP ,'POST').subscribe(
        (res) => resolve(res),
        (err) => reject(err)
      );
    });
  }
  changeForgetPassword(data:{}){
    return new Promise((resolve, reject) => {
      this.httpClient.call(data, UrlConstants.changeOtpEmailPassword ,'POST').subscribe(
        (res) => resolve(res),
        (err) => reject(err)
      );
    });
  }
}
